import React, { useState, useEffect } from "react";
import Assets from "../assets";

function Wallets({ port }) {
    const [data, setData] = useState("");
    const [usdt, setUsdt] = useState(0);
    const [preUsdt, setPreUsdt] = useState(0);

    const totals = {
        balance: usdt,
        assets: port.assets,
    };

    const deposit = () => {
        setUsdt(usdt + parseInt(preUsdt));
    };

    return (
        <div className="container mb-5">
            <h1 className="text-center">Wallets</h1>
            <p className="col">
                Total Balance{" "}
                <button type="button" className="btn btn-info">
                    history
                </button>
            </p>
            <div className="d-flex gap-2">
                <div>
                    {" "}
                    <p className="col">{totals.balance}</p>
                    <p>Today's PNL</p>
                    <p>0 (+0.00%)</p>
                </div>
            </div>
            <div className="d-flex gap-3 mb-5">
                <button type="button" className="btn btn-warning">
                    Convert
                </button>
                <button type="button" className="btn btn-info">
                    Deposit
                </button>
            </div>
            <p>
                <u>Assets</u>
            </p>

            <div className="row">
                <div className="col-2">
                    <img
                        width="35px"
                        src="https://cryptologos.cc/logos/binance-usd-busd-logo.png?v=022"
                    />
                </div>
                <div className="col-5">
                    <div className="col">BUSD</div>
                </div>
                <div className="col-5">
                    <div className="row">
                        <div className="col">142.231</div>
                    </div>
                </div>
            </div>
            {totals.assets.map((item, index) => (
                <div className="row mt-3" key={index}>
                    <div className="col-2">
                        <img width="35px" src={item.logo} />
                    </div>
                    <div className="col-5">
                        <div className="col">{item.symbol}</div>
                    </div>
                    <div className="col-5">
                        <div className="row">
                            <div className="col">{item.amount}</div>
                        </div>
                        <Assets
                            setUsdt={setUsdt}
                            symbol={item.symbol}
                            amount={item.amount}
                        />
                    </div>
                </div>
            ))}

            <hr />
            <p>
                <b>Use BNB for fee (10%) discount </b>
            </p>

            {/* <div className="deposit">
                <p>Deposit Quantity</p>
                <input
                    value={preUsdt}
                    onChange={(e) => setPreUsdt(e.target.value)}
                    className="form-control"
                    type="text"
                />
                <button onClick={deposit} className="mt-2 btn btn-primary">
                    Deposit To Wallets
                </button>
            </div>
            <div className="withdraw">
                <p>withdraw Quantity</p>
                <input className="form-control" type="text" />
                <button className="mt-2 btn btn-danger">
                    Withdraw To Wallets
                </button>
            </div> */}
        </div>
        // <div className="container">
        //     <div className="row">
        //         <div className="col-11 mb-5">
        //             <h1>Wallets</h1>
        //         </div>
        //         <div className="col-1">
        //             <button type="button" className="btn btn-info">
        //                 ประวัติ
        //             </button>
        //         </div>
        //     </div>
        //     <div className="row">
        //         <div className="col-2">
        //             <img
        //                 width="35px"
        //                 src="https://cryptologos.cc/logos/tether-usdt-logo.png?v=022"
        //             />
        //         </div>
        //         <p className="col">0 USDT</p>
        //     </div>
        //     <div className="d-flex gap-3 mb-5">
        //         <button type="button" className="btn btn-primary">
        //             ฝาก
        //         </button>
        //         <button type="button" className="btn btn-danger">
        //             ถอน
        //         </button>
        //     </div>
        //     <div className="row">
        //         <h2>Assets</h2>
        //     </div>
        // </div>
    );
}

export default Wallets;
