import React, { useState, useEffect } from "react";
import { Routes, Route, useParams } from "react-router-dom";
import TradeViewChart from "react-crypto-chart";

function Trades() {
    const [data, setData] = useState("");
    const [position, setPosition] = useState(false);
    const [price, setPrice] = useState(1);
    const [usdt, setUsdt] = useState(0);
    let params = useParams();

    useEffect(() => {
        const url = `https://api.binance.com/api/v1/ticker/24hr?symbol=${params.id}`;

        const fetchData = async () => {
            try {
                const response = await fetch(url);
                const json = await response.json();
                setData(json);
            } catch (error) {
                console.log("error", error);
            }
        };

        fetchData();
    }, []);

    return (
        <div className="container">
            <h1>Spot</h1>
            <TradeViewChart
                containerStyle={{
                    minHeight: "300px",
                    minWidth: "300px",
                    marginBottom: "30px",
                }}
                interval="15m"
                pair={params.id}
            />
            <div class="row g-5">
                <div class="col">
                    <div class="row g-3">
                        <div className="col">
                            <h4 class="mb-3">{params.id}</h4>
                        </div>
                        <div className="col">
                            <p>{data.lastPrice}</p>
                        </div>
                        <div className="col">
                            <p
                                className={
                                    data.priceChangePercent < 0
                                        ? "text-danger"
                                        : "text-success"
                                }
                            >
                                {data.priceChangePercent}%
                            </p>
                        </div>
                    </div>
                    <div class="my-3">
                        <button
                            onClick={() => setPosition(true)}
                            class="btn btn-success"
                        >
                            Buy
                        </button>{" "}
                        <button
                            onClick={() => setPosition(false)}
                            class="btn btn-danger"
                        >
                            Sell
                        </button>
                    </div>
                    <form onSubmit={(e) => e.preventDefault()} class="needs-validation" novalidate>
                        <div class="row g-3">
                            <div class="col-sm-12">
                                <input
                                    type="number"
                                    class="form-control"
                                    onChange={(e) => setPrice(e.target.value)}
                                    placeholder={`${data.lastPrice}`}
                                    value={price}
                                    required
                                />
                            </div>
                            <div class="col-sm-12">
                                <input
                                    type="text"
                                    class="form-control"
                                    id="firstName"
                                    placeholder={`Amount (${params.id})`}
                                    value={usdt/price}
                                    required
                                />
                            </div>
                            <div class="col-sm-12">
                                <input
                                    type="number"
                                    class="form-control"
                                    onChange={(e) => setUsdt(e.target.value)}
                                    placeholder="Total (USDT)"
                                    value={usdt}
                                    required
                                />
                                <small class="text-muted">
                                    Avbl 40,300 USDT
                                </small>
                            </div>
                        </div>
                        <button
                            className={
                                position === true
                                    ? "w-100 btn btn-success btn-lg"
                                    : "w-100 btn btn-danger btn-lg"
                            }
                            type="submit"
                        >
                            {position === true ? "Buy" : "Sell"} {params.id}
                        </button>
                    </form>
                </div>
            </div>
            <div className="row mt-3">
                <div className="col">
                    <button className="btn">Open order(0)</button>
                </div>
                <div className="col">
                    <button className="btn">Funs</button>
                </div>
            </div>
            <hr class="my-4" />
            <div className="row">
                <div className="col">
                    <p>{params.id}</p>
                </div>
                <div className="col">
                    <p>{new Date().toUTCString()}</p>
                </div>
                <p className="text-success">
                    <b>Buy</b>
                </p>
            </div>
            <div className="row">
                <div className="col">
                    <p>Price</p>
                </div>
                <div className="col">
                    <p>0.5984</p>
                </div>
            </div>
            <div className="row">
                <div className="col">
                    <p>Filled</p>
                </div>
                <div className="col">
                    <p>72</p>
                </div>
            </div>
            <div className="row">
                <div className="col">
                    <p>Total</p>
                </div>
                <div className="col">
                    <p>43</p>
                </div>
            </div>
            <hr class="my-4" />
        </div>
    );
}

export default Trades;
